@font-face {
  font-family: 'Tilt Neon';
  src: url(fonts/TiltNeon-Regular.ttf);
}
@font-face {
  font-family: 'Lato-Regular';
  src: url(fonts/Lato-Regular.ttf);
}
@font-face {
  font-family: 'Lato-Black';
  src: url(fonts/Lato-Black.ttf) format("truetype");
}
@font-face {
  font-family: 'Lato-Bold';
  src: url(fonts/Lato-Bold.ttf) format("truetype");
}
.highlight{
  background: linear-gradient(to right, #ff5c00,#ff7c00 50%,#ffb801 80%, #fff202);
}
.highlightText{
  background: linear-gradient(to right, #ff5c00,#ff5c00 50%,#ffb801 80%, #fff202);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
body{
  margin: 0 !important;
  padding: 0 !important;
  font-family: Lato-Regular;
  background-color: #000F2C;
  color: white;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  font-size: 16px;
  transition: .25s ease-in-out;
  overflow: overlay;
}
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  display: none;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: white; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.785); 
}
.TopPage{
  background: url('./images/Backgrounds/HeaderBackground.webp') no-repeat;
  background-size: 100% 100%;
}
button, header, .About{
  letter-spacing: 1px;
}
.MyName{
  font-size: 96px;
  position: relative;
  font-family: 'Lato-Black', sans-serif;
}
.MyName:after {
  position: absolute;
  content: '';
  height: 8px;
  bottom: -20px;
  left: 35%;
  width: 30%;
  background-color: #FF5C00;
}
.ContactButton{
  position: absolute;
  top: 10px;
  right:10px;
  z-index: 1;
}
nav{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 5px;
  font-size: 14px;
  text-align: center;
  align-items: center;
}

.tab{
  text-decoration: none;
  color: white;
  height: 100%;
  display: grid;
  align-items: center;
}
.tab:hover{
  color:#FF5C00;
}
header{
  position: sticky;
  padding-top: 15px;
  top: 0;
  display: flex;
  justify-content: center;
  font-size: 14px;
  color: white;
  width: 100%;
  z-index: 0;
  user-select: none;
}
header > p{
  display: none;
}
.SectionTitle{
  font-size: 64px;
  font-family: Lato-Black;
  font-weight: 900;
}
article{
  margin: 120px 0;
  padding-top: 2vh;
}
.About{
  user-select: none;
  text-align: center;
  background: url('./images/Backgrounds/Pulse1.webp') no-repeat;
  background-size: contain;
  background-position: center;
  /* padding-top: 60px; */
}
.AboutDetails{
  width: 95%;
  margin: 0 auto;
  display: grid;
  grid-template-columns:  repeat(auto-fill, minmax(500px, 1fr));
  align-items: center;
  justify-items: center;
}
.AboutDetailsDesc{
  text-align: left;
}
.AboutDetailsImage{
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
}
.TitleDetails{
  align-self: center;
  text-align: center;
}
.Skills{
  background: url('./images/Backgrounds/Pulse3.webp') no-repeat;
  background-position: center;
}
.SkillList{
  width: 90%;
  margin: 0 auto;
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr 1fr 1fr; */
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  justify-content: center;
  gap: 21px;
}
.SkillListElement{
  text-align: center;
  background-color: #141757;
  box-shadow: black 0 0 5px;
  padding: 40px;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}
.SkillListElement::before{
  content: 'e';
  position: absolute;
  height: 110%;
  width: 30px;
  background: white;
  box-shadow: 0 0 10px #fff;
  filter: blur(5px);
  opacity: 0.75;
  top: -15px;
  transition: 0.5s;
  transform: translateX(-200px) rotate(-20deg) ;
}
.SkillListElement:hover::before{
  transform: translateX(250px) rotate(-20deg) ;
}
.SkillListElement > p {
  font-size: 20px;
  margin: 20px 0 5px 0;
}
.SkillListElement > img {
  border-radius: 20px;
  margin-top: 5px;
}

.Title {
  margin-top: 60px;
}   

.Resume{
  background: url('./images/Backgrounds/Pulse2.webp') no-repeat;
  background-position: right;
}
.ResumeGrid{
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 90%;
  row-gap: 86px;
  margin: 0 auto;
}
.ResumeGridSection{
  width: 90%;
  margin: 0 auto;
}
.ResumeGridSectionTitle{
  font-size: 24px;
  font-weight: bold;
  position: relative;
  letter-spacing: 2px;
}
.ResumeGridSectionTitle:after {
  position: absolute;
  content: '';
  height: 3px;
  bottom: -10px;
  left: 0;
  width: 100px;
  background-color: #FF5C00;
}
.ResumeGridSectionList{
  display: flex;
  flex-flow: column;
  row-gap: 44px;
}
.ResumeGridSectionListElement{
  display: flex;
  gap: 24px;
  align-items: center;
  position: relative;
  z-index: 1;
}
.ResumeGridSectionListElement:after {
  position: absolute;
  content: '';
  z-index: -1;
  height: 55px;
  bottom: -50px;
  left: 50px;
  width: 3px;
  background-color: #FF5C00;
}
.ResumeGridSectionListElement:last-child:after {
  display: none;
}
.ResumeGridSectionListElementImg{
  border-radius: 20px;
}
.ResumeGridSectionListElementDesc > h3, .ResumeGridSectionListElementDesc > p{
  margin: 0px;
}
.ResumeGridSectionListElementDesc > h3{
  font-size: 20px;
}
button{
  padding: 13px;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  border: none;
  border-radius: 15px;
}
.DownloadButton{
  padding: 15px 40px;
  border-radius: 25px;
  letter-spacing: 2px;
  color: white;
}
.DownloadButton:hover , button:hover{
  filter: brightness(75%);
}
.ProjectDetailNavButton{
  background-color:#FF5C00;
  color: white;
  border-radius: 1em;
  font-size: 20px;
  padding: 5px;
  margin: 16px;
}
.ContactsArticle{
  background: url('./images/Backgrounds/Pulse5.webp') no-repeat;
  background-position: cover;
  background-position-x: center;
  background-position-y: -50px;
  margin-bottom: 0px;
}
.ContactLink> img{
  border-radius: 20px;
  width: 90%;
}
.ContactsEmail{
  width: 80%;
  margin: 0 auto;
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  align-items: center;
  background: linear-gradient(to right, #ff5c00,#ff7c00 50%,#ffb801 80%, #fff202);
  padding: 30px;
  margin-bottom: 100px;
  border-radius: 20px;
}
.ContactsEmailInfo{
  margin-left: 20px;
  margin-bottom: 20px;
  text-align: center;
  display: flex;
  flex-flow: column;
  row-gap: 10px;
  justify-content: center;
  height: 80%;
}

.emailButton{
  width: 100%;
  font-size: 20px;
  border-radius: 12px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.Projects{
  position: relative;
  background: url('./images/Backgrounds/Pulse4.webp') no-repeat;
  background-position: center;
  background-size: contain;
}
.Project{
  background-color: #141757;
  position: relative;
  border-radius: 20px;
  box-shadow: 0px 0px 10px black;
  transition: ease-in-out .25s;
}
.Project > section > p{
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 2px;
  color: #FF5C00;
}
.Project > section{
  width: 90%;
  margin: 0 auto;
  padding-bottom: 30px;
}
.ProjectImage{
  width: 100%;
  aspect-ratio: 3 / 1.5;
  border-radius: 20px 20px 0 0;
}
#ProjectDetail{
  height: 100vh;
  width: 600px;
  top: 0;
  overflow-y: auto;
  background-color: #141757;
  transition: ease-in-out .25s;
  border: none;
  position: fixed;
  /* left: calc(100% - 600px); */
}
.appear{
  left: calc(100vw - 600px);
}
.disappear{
  left: 100%;
}
.modalDetail{
  width: 90%;
  margin: 0 auto;
}
.modalDetail > a{
  text-decoration: none;
  color: white;
}
.CategoryList{
  display: flex;
  column-gap: 9px;
}
.CategoryList > p {
  background-color: #FF5C00;
  padding: 6px 12px;
  border-radius: 1em;
  margin: 0;
}
.ProjectDetailHeader{
  letter-spacing: 2px;
  font-size: 18px;
  margin-bottom: 8px;
}
.slick-slide > div{
  margin: 10px 40px;
}
.ProjectCollection{
  width: 90%;
  margin: 0 auto;
}
.ProjectButton{
  position: absolute;
  right: 10px;
  top: 0px;
  background-color: rgb(16, 13, 29);
  padding: 5px;
  font-size: 16px;
  border-radius: 10px;
  border: 2px solid white;
  transition: .25s ease-in-out;
}
.ProjectButton:hover{
  background-color: black;
  border-radius: 0px;
}
.Project:hover{
  filter: brightness(70%);
}
.ProjectFooter{
  width: 95%;
  display: grid;
  margin: 0 auto;
  align-items: center;
}
.ProjectFooterQRCode{
  display: none;
  width: 80%;
}
.ProjectFooterText{
  text-align: center;
  padding-bottom: 20px;
}
dialog{
  width: 80%;
  box-shadow: 0px 0px 10px black;
  color: white;
  background-color: rgb(16, 13, 29);
}
dialog::backdrop{
  background-color: rgba(0, 0, 0, .4);
  backdrop-filter: blur(4px);
}
input[type=text], input[type=email], textarea{
  padding: 15px;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  margin-bottom: 24px;
  font-family: sans-serif;
}
input[type=email],textarea{
  width: calc(100%-15px);
}
textarea{
  min-height: 145px;
}
.nameInput{
  width: 100%;
}
/* input[type=text]:nth-child(2){
  width: 500px;
} */
form{
  display: grid;
}
.ContactsEmail > img {
  width: 100%;
  border-radius: 20px;
  border: 3px solid white;
  background-color: red;
}
.ContactsEmailInfo{
  display: flex;
  flex-flow: row;
  column-gap: 67px;
}

@media (max-width: 1000px) {
  .ResumeGrid{
    grid-template-columns: 1fr;
  }
  .ResumeGridSectionTitle{
    text-align: center;
  }
  .ResumeGridSectionTitle:after{
    left: calc(50% - 50px)
  }
  .MyName{
    /* font-size: 50px; */
  }
  .AboutDetailsDesc{
    text-align: center;
  }
  .ContactsEmail > img{
    display: none;
  }
}